import { useState, useEffect, useRef, useCallback } from "react";
import Sidebar from "../components/Sidebar";
import Head from "next/head";
import "./globals.css";

export default function RootLayout({ children }: { children: React.ReactNode }) {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [isSmallScreen, setIsSmallScreen] = useState(false);
  const [isClient, setIsClient] = useState(false); // New state to track client-side rendering
  const touchStartX = useRef(0);

  useEffect(() => {
    setIsClient(true); // Set to true after the component mounts
  }, []);

  // Kiểm tra khi client đã sẵn sàng và cập nhật trạng thái màn hình
  useEffect(() => {
    if (isClient) {
      const handleResize = () => {
        setIsSmallScreen(window.innerWidth < 640);
      };
      handleResize(); // Kiểm tra kích thước màn hình ngay khi mount
      window.addEventListener("resize", handleResize); // Lắng nghe sự kiện thay đổi kích thước màn hình
      return () => window.removeEventListener("resize", handleResize); // Dọn dẹp sự kiện khi unmount
    }
  }, [isClient]);

  const toggleSidebar = useCallback(() => {
    setIsSidebarOpen((prev) => !prev);
  }, []);

  const handleTouchStart = useCallback((e: React.TouchEvent) => {
    touchStartX.current = e.touches[0].clientX;
  }, []);

  useEffect(() => {
    const handleTouchMove = (e: TouchEvent) => {
      const touchEndX = e.touches[0].clientX;
      if (touchStartX.current - touchEndX > 100) {
        setIsSidebarOpen(false); // Đóng sidebar khi swipe trái
      }
    };

    if (isSidebarOpen) {
      document.addEventListener("touchmove", handleTouchMove);
    } else {
      document.removeEventListener("touchmove", handleTouchMove);
    }

    return () => {
      document.removeEventListener("touchmove", handleTouchMove); // Dọn dẹp sự kiện khi unmount
    };
  }, [isSidebarOpen]);

  return (
    <html lang="en">
      <Head>
        <title>Camera Manager</title>
        <meta name="description" content="Manage and monitor your cameras efficiently" />
      </Head>
      <body className="h-screen overflow-hidden bg-[#343940]">
        <div className="flex h-full relative">
          {/* Chỉ render Sidebar khi đang ở client */}
          {isClient && (
            <div
              aria-hidden={!isSidebarOpen}
              className={`${isSmallScreen ? "hidden" : "block"} transform transition-all duration-300 ease-in-out z-50 fixed inset-0 sm:relative sm:block w-64 bg-gray-800 text-white`}
              onTouchStart={handleTouchStart}
            >
              <Sidebar />
            </div>
          )}

          {/* Nút mở Sidebar cho màn hình nhỏ */}
          {isSmallScreen && !isSidebarOpen && (
            <button
              aria-label="Toggle Sidebar"
              className="sm:hidden p-3 text-white bg-gray-700 rounded-full fixed top-4 left-4 z-50 hover:bg-gray-600"
              onClick={toggleSidebar}
            >
              <span className="block w-5 h-1 bg-white mb-1"></span>
              <span className="block w-5 h-1 bg-white mb-1"></span>
              <span className="block w-5 h-1 bg-white"></span>
            </button>
          )}

          <div className="flex-1 overflow-auto p-6 main-content transition-all duration-300 z-10">
            {children}
          </div>
        </div>
      </body>
    </html>
  );
}
